'use client'

import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"

const siteName = process.env.NEXT_PUBLIC_SITE_NAME || "Stampi"

export default function AboutPage() {
    return (
        <main className="bg-white">

            {/* ---------- HERO ---------- */}
            <section className="max-w-7xl mx-auto px-6 py-20 text-center">
                <h1 className="text-4xl md:text-5xl font-bold text-primary mb-6">
                    About {siteName}
                </h1>
                <p className="text-lg text-gray-600 max-w-3xl mx-auto">
                    <span className='logo-font text-primary'>{process.env.NEXT_PUBLIC_SITE_NAME ?? "site_name"}</span> is built to help local businesses turn everyday customers
                    into loyal regulars — without complicated apps or systems.
                </p>
            </section>

            {/* ---------- STORY ---------- */}
            <section className="bg-light-shade/60 py-20">
                <div className="max-w-5xl mx-auto px-6">
                    <Card className="border-0 shadow-sm p-8 md:p-12 rounded-2xl">
                        <h2 className="text-2xl font-semibold mb-4 text-primary">
                            Our Story
                        </h2>
                        <p className="text-gray-600 leading-relaxed mb-4">
                            We started {siteName} after seeing how difficult it was for small
                            shopkeepers to retain customers. Paper stamp cards get lost,
                            mobile apps are ignored, and complex systems slow business down.
                        </p>
                        <p className="text-gray-600 leading-relaxed">
                            Our goal is simple: give businesses an easy way to reward
                            customers and encourage repeat visits — using tools they already
                            understand.
                        </p>
                    </Card>
                </div>
            </section>

            {/* ---------- MISSION & VALUES ---------- */}
            <section className="max-w-7xl mx-auto px-6 py-20">
                <h2 className="text-3xl font-semibold text-center mb-14">
                    What We Believe In
                </h2>

                <div className="grid md:grid-cols-3 gap-8">
                    <ValueCard
                        title="Simple for Everyone"
                        text="No learning curve, no complex setup. If you can use WhatsApp or UPI, you can use Stampi."
                    />
                    <ValueCard
                        title="Built for Local Businesses"
                        text="Designed specifically for cafés, salons, grocery stores, and small retailers."
                    />
                    <ValueCard
                        title="Real Business Impact"
                        text="Every feature is focused on increasing repeat customers and long-term growth."
                    />
                </div>
            </section>

            {/* ---------- WHO IT’S FOR ---------- */}
            <section className="bg-light-shade/60 py-20">
                <div className="max-w-4xl mx-auto px-6 text-center">
                    <h2 className="text-3xl font-semibold mb-6">
                        Who Uses {siteName}?
                    </h2>
                    <p className="text-gray-600 text-lg leading-relaxed">
                        From cafés and salons to retail stores and service providers,
                        {siteName} is trusted by businesses that care about building lasting
                        customer relationships.
                    </p>
                </div>
            </section>

            {/* ---------- CTA ---------- */}
            <section className="py-20">
                <div className="max-w-4xl mx-auto px-6">
                    <div className="bg-primary rounded-2xl text-white text-center py-14 px-6">
                        <h2 className="text-3xl font-semibold mb-4">
                            Start Building Customer Loyalty
                        </h2>
                        <p className="mb-8 text-white/90">
                            Join shopkeepers using {siteName} to grow repeat customers.
                        </p>
                        <Button asChild size="lg" className="bg-white text-primary hover:bg-light-shade">
                            <Link href="/signup">Start Free Trial</Link>
                        </Button>
                    </div>
                </div>
            </section>

        </main>
    )
}

/* ---------- COMPONENT ---------- */

const ValueCard = ({ title, text }) => {
    return (
        <div className="bg-white rounded-xl p-6 shadow-sm hover:shadow-md transition">
            <h3 className="font-semibold text-lg mb-3">{title}</h3>
            <p className="text-gray-600 text-sm leading-relaxed">
                {text}
            </p>
        </div>
    )
}
